.app-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #030303;
  position: relative;
  overflow-x: hidden;
  background: url("../public/milkyWay.jpg") center center / cover no-repeat fixed;
}

.planet-animation-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  overflow: hidden;
}

.mercury-animation-wrapper { width: 6vh; height: 6vh; left: 15vh; }
.venus-animation-wrapper { width: 17vh; height: 17vh; left: 45vh; }
.earth-animation-wrapper { width: 25vh; height: 25vh; }
.mars-animation-wrapper { width: 9vh; height: 9vh; right: 64vh; }
.jupiter-animation-wrapper { width: 50vh; height: 50vh; right: 0; }

.planet-animation {
  border-radius: 50%;
  cursor: pointer;
  object-fit: cover;
  background-color: transparent;
}

.mercury-animation { width: 6vh; height: 6vh; }
.venus-animation { width: 17vh; height: 17vh; }
.earth-animation { width: 25vh; height: 25vh; }
.mars-animation { width: 9vh; height: 9vh; }
.jupiter-animation { width: 50vh; height: 50vh; }

.planet-box {
  background: rgba(78, 78, 78, 0.25);
  border-radius: 16px;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  padding: 40px;
  width: 65vh;
  height: 50vh;
}

.mercury-box { border: 3px solid rgba(183, 184, 185, 1); }
.venus-box { border: 3px solid rgba(165, 124, 27, 1); }
.earth-box { border: 3px solid rgba(5, 51, 85, 1); }

.right-button {
  right: -1vh
}

.left-button {
  left: -1vh
}

.left-button, .right-button {
  background: none;
  border: none;
  color: white;
  font-size: 3vh;
  cursor: pointer;
  position: absolute;
  top: 25vh;
  transform: translateY(-50%);
  z-index: 1;
}

.mars-box { border: 3px solid rgba(69, 24, 4, 1); }
.jupiter-box { border: 3px solid rgba(216, 202, 157, 1); }

.popup-content {
  position: relative;
  text-align: center;
  color: white;
  font-family: 'Kode Mono', monospace;
  font-size: 2em;
  top: -4vh;
}

.close-icon {
  position: absolute;
  top: 1vh;
  right: 0;
  width: 2vh;
  height: 2vh;
  cursor: pointer;
}

.footer-text {
  position: absolute;
  bottom: 10vh;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-family: "Orbitron", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-size: 3vh;
  text-align: center;
  letter-spacing: 0.8vh;
}

.about-me {
  position: relative;
  text-align: center;
  color: white;
  font-family: 'Kode Mono', monospace;
  font-size: 1.8vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 5vh;
  margin-top: 7vh;
  margin-bottom: 10vh;
}

.social-button {
  width: 3.1vh;
  height: 3.1vh;
  border-radius: 50%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s ease;
}

.social-button:hover {
  transform: scale(1.2);
}

.social-icon {
  width: 1.8vh;
  height: 1.8vh;
}

.projects {
  position: relative;
  text-align: center;
  color: white;
  font-family: 'Kode Mono', monospace;
  font-size: 1.8vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 65%;
}

.mini-planet-wrapper {
  bottom: 2vh;
  left: 100vh;
  transform: translateX(-50%);
  display: flex;
  gap: 5vh;
  justify-content: center;
  align-items: center;
  position: absolute;
  overflow: visible;
}

.mini-planet-animation {
  cursor: pointer;
}

.mini-planet:hover {
  transform: scale(1.05);
  transition: transform 0.3s ease;

}

.mercury-mini {
  width: 4vh;
  height: 4vh;
  display: flex;
}

.venus-mini {
  width: 6vh;
  height: 6vh;
}

.earth-mini {
  width: 7vh;
  height: 7vh;
}

.mars-mini {
  width: 5vh;
  height: 5vh;
}

.jupiter-mini {
  width: 12vh;
  height: 12vh;
}

.disabled {
  pointer-events: none;
}

.planet-box .carousel-slide {
  position: relative;
  text-align: center;
  color: white;
  font-family: 'Kode Mono', monospace;
  font-size: 1.7vh;
  height: 100%;
}

.skills-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2vh;
  color: white;
  font-family: 'Kode Mono', monospace;
  max-width: 80vw;
  max-height: 80vh;
  box-sizing: border-box;
}

.skills-columns {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  margin-bottom: 2vh;
  margin-top: -5vh;
}

.skills-column {
  flex: 1;
  margin: 1vh 2vw;
}

.skills-column h3, .skills-other h3 {
  text-align: center;
  font-size: 2vh;
}

.skills-column ul, .skills-other ul {
  list-style-type: none;
  padding: 0;
  text-align: center;
}

.skills-column li, .skills-other li {
  margin: 1vh 0;
  font-size: 1.8vh;
}

.skills-other {
  width: 100%;
  max-width: 60vw;
  text-align: center;
  margin-top: -2vh;
}

.skills-other p {
  margin: 2vh 0;
  font-size: 1.5vh;
}

.slide-counter {
  position: absolute;
  bottom: -4vh;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.5vh;
  color: white;
}
